@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
 /* base style */
 * {
   margin: 0;
   font-family: "Quicksand";
   color: #333;
 }
 .navbar {
  padding: 10%;
  background-color: cornflowerblue;
  display: flex;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
 }
 .navbar h1 {
  color: lightgreen;
 }
 .navbar .links {
  margin-left: auto;
 }
 .navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  font-size: 20px;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;

 }
.navbar a:hover {
  color: #f1356d;
  
}
.content {
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
}


/* blogs previews / list */
.blog-preview {
  padding: 10px 16px;
  margin: 20px 0;
border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
  background-color: whitesmoke;
}

.blog-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}

.blog-preview a{
  text-decoration: none;
}


/* Blog Details page */
.blog-details h2 {
  font-size: 20px; 
  color: #f1356d;
  margin-bottom: 10px;
}
.blog-details div {
  margin: 20px 0;
}


.blog-details button {
  background: red;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}


/* Create New Blog Form  */
   .create {
max-width: 400px;
margin: 0 auto;
text-align: center;
}
.create label {
  text-align: left;
  display: block;  
  color: #008000;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0px;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: skyblue;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;

}

.blog-model {
  background-color: #008000;
}
